import { useEffect, useState } from "react";
import { createBrowserRouter } from "react-router";
import { getMenu } from "../api/users";
import { generateRoutes } from "../utils/generatesRoutes";
import { useAppDispatch, useAppSelector } from "../store/hooks";
import { setMenu } from "../store/login/authSlice";
import { routes } from "../router";

interface MenuItem {
    key: string;
    label: string;
    icon: string;
    children?: MenuItem[];
}

const isProduction = process.env.NODE_ENV === "production";
const basename = isProduction ? "/react-project" : undefined;

export function useAppRouter() {
    const { token } = useAppSelector((state) => state.authSlice);
    const dispatch = useAppDispatch();
    const [router, setRouter] = useState<ReturnType<typeof createBrowserRouter> | null>(null);

    useEffect(() => {
        const loadRoutes = async () => {
            try {
                const baseRoutes = [...routes];

                if (!token) {
                    console.log(" ");
                    setRouter(createBrowserRouter(baseRoutes));
                    return;
                }

                const { data } = await getMenu<MenuItem[]>();
                console.log(" getMenu ", data);

                if (data.length) {
                    dispatch(setMenu(data));
                    const dynamicRoutes = generateRoutes(data);
                    console.log(" ", dynamicRoutes);

                    const homeRoute = baseRoutes.find(r => r.path === "/");

                    if (homeRoute) {
                        if (!Array.isArray(homeRoute.children)) {
                            homeRoute.children = [];
                        }

                        const existingIndex = homeRoute.children.find(r => r.index);
                        homeRoute.children = [
                            ...(existingIndex ? [existingIndex] : []),
                            ...dynamicRoutes,
                        ];

                        console.log("  / children", homeRoute.children);
                    }
                }

                console.log(" ", baseRoutes);
                const routerInstance = createBrowserRouter(baseRoutes, { basename });
                setRouter(routerInstance);
            } catch (error) {
                console.error(" ", error);
                setRouter(createBrowserRouter(routes, {
                    basename
                }));
            }
        };

        loadRoutes();
    }, [token, dispatch]);

    return router;
}
