interface MockRequestOptions {
    body: string;
    url?: string;
    type?: string;
    headers?: {
        [key: string]: string;
    };
}

Mock.mock("https://www.demo.com/login", "post", (options: MockRequestOptions) => {
    //console.log("options",options.body)
    const { username, password } = JSON.parse(options.body)
    if (username === "admin" && password === "admin") {
        return {
            code: 200,
            message: "",
            data: {
                username: "admin",
                token: "mocktoken123456admin",
                btnAuth: ["add", "edit", 'delete']
            }
        }
    } else if (username === "manager" && password === "manager") {
        return {
            code: 200,
            message: "",
            data: {
                username: "manager",
                token: "mocktoken123456manager",
                btnAuth: ["add", "edit"]
            }
        }
    } else if (username === "user" && password === "user") {
        return {
            code: 200,
            message: "",
            data: {
                username: "user",
                token: "mocktoken123456user",
                btnAuth: ["add"]
            }
        }
    } else {
        return {
            code: 401,
            message: "",
            data: ""
        }
    }
})


Mock.mock('https://www.demo.com/menu', "get", (_options: MockRequestOptions) => {
    const token = sessionStorage.getItem("token");
    if (token === "mocktoken123456admin") {
        return {
            code: 200,
            message: '',
            data: menuList
        }
    } else if (token === "mocktoken123456user") {
        return {
            code: 200,
            message: '',
            data: userMenuList
        }
    } else if (token === "mocktoken123456manager") {
        return {
            code: 200,
            message: '',
            data: managerMenuList
        }
    } else {
        return {
            code: 200,
            message: "",
            data: []
        }
    }
})

