//
function generateRooms() {
    const rooms = [];
    for (let i = 0; i < 50; i++) {
        const floor = 1 + Math.floor(i / 6); // 6
        const roomNumber = floor * 100 + (101 + (i % 6)); //
        rooms.push({
            roomNumber,
            decorationType: Mock.Random.pick(['', '']),
            area: Mock.Random.integer(70, 300),
            unitPrice: Mock.Random.integer(1, 3),
            src: 'https://zos.alipayobjects.com/rmsportal/jkjgkEfvpUPVyRjUImniVslZfWPnJuuZ.png'
        });
    }
    return rooms;
}

Mock.mock('https://www.demo.com/roomList', 'post', (options: MockRequestOptions) => {
    console.log("id", JSON.parse(options.body).roomid)
    return {
        code: 200,
        message: "",
        data: {
            rooms: generateRooms()
        }
    };
});

